import {updateSliderValues} from './main';

class VerticalGradientSlider {
  constructor(sliderId, tooltipClass) {
    this.slider = document.getElementById(sliderId);
    this.thumbs = this.slider.querySelectorAll('.thumb');
    this.tooltip = document.querySelector(tooltipClass);

    this.initializeSlider();
  }

  initializeSlider() {
    this.updateSliderBackground();

    this.thumbs.forEach(thumb => {
      thumb.addEventListener('mousedown', (event) => {
        event.preventDefault();

        const rect = this.slider.getBoundingClientRect();
        const initialPosition = (event.clientY - rect.top) / this.slider.offsetHeight;
        const thumbIndex = Array.from(this.thumbs).indexOf(thumb);
        if (thumbIndex==0)  thumb.setAttribute('data-value', `${Math.round(0.00 * 100)}%`);
        else if (thumbIndex==1)  thumb.setAttribute('data-value', `${Math.round(1.00 * 100)}%`);
        else thumb.setAttribute('data-value', `${Math.round(initialPosition * 100)}%`);
        this.updateTooltipPosition(thumb);

        const onMouseMove = (event) => this.onMouseMove(thumb, event);
        const onMouseUp = () => {
          document.removeEventListener('mousemove', onMouseMove);
          document.removeEventListener('mouseup', onMouseUp);
          this.updateSliderBackground();
          this.tooltip.style.display = 'none';
        };

        document.addEventListener('mousemove', onMouseMove);
        document.addEventListener('mouseup', onMouseUp);
      });

      thumb.addEventListener('mouseover', () => {
        const value = thumb.getAttribute('data-value');
        this.tooltip.innerText = value;
        this.updateTooltipPosition(thumb);
        this.tooltip.style.display = 'block';
      });

      thumb.addEventListener('mouseout', () => {
        this.tooltip.style.display = 'none';
      });
    });
  }

  onMouseMove(thumb, event) {
    const thumbIndex = Array.from(this.thumbs).indexOf(thumb);

    // Check if the thumb is the second or third one (index 1 or 2)
    if (thumbIndex === 1 || thumbIndex === 2) {
      const rect = this.slider.getBoundingClientRect();
      let newPosition = (event.clientY - rect.top) / this.slider.offsetHeight;
      newPosition = Math.max(0, Math.min(1, newPosition));

      const prevThumbs = Array.from(this.thumbs).filter(t => t !== thumb);
      const prevPositions = prevThumbs.map(t => t.offsetTop / this.slider.offsetHeight);
      const minSeparation = 0.05;

      const conflicts = prevPositions.some(pos => Math.abs(newPosition - pos) < minSeparation);

      if (!conflicts) {
        thumb.style.top = `${newPosition * 100}%`;
        thumb.setAttribute('data-value', `${Math.round(newPosition * 100)}%`);
        this.updateTooltipPosition(thumb);
        this.tooltip.style.display = 'block';
        updateSliderValues(this.getThumbValues());

      }
    }
  }

  updateSliderBackground() {
    const positions = Array.from(this.thumbs).map(thumb => thumb.offsetTop / this.slider.offsetHeight);
    const gradient = Array.from(this.thumbs).map((thumb, index) => `${thumb.style.backgroundColor} ${positions[index] * 100}%`).join(', ');
    this.slider.style.background = `linear-gradient(to bottom, ${gradient})`;
  }

  updateTooltipPosition(thumb) {
    const decimalValue = parseFloat(thumb.getAttribute('data-value')).toFixed(2);
    this.tooltip.innerText = decimalValue/100.0;
    this.tooltip.style.left = `${thumb.offsetLeft - this.tooltip.offsetWidth / 2 + thumb.offsetWidth / 2}px`;
    this.tooltip.style.top = `${thumb.offsetTop - this.tooltip.offsetHeight - 10}px`;
  }

  getThumbValues() {
      const positions = Array.from(this.thumbs).map(thumb => parseFloat(thumb.getAttribute('data-value')) / 100);
      return positions;
    }
}

export default VerticalGradientSlider;



