import { slider_values, updateSingleThumbValue } from './main';
var THREE = require("three");

var frameColors = [
  new THREE.Color("hsl(0, 70%, 70%)"),
  new THREE.Color("hsl(60, 70%, 70%)"),
  new THREE.Color("hsl(120, 70%, 70%)"),
  new THREE.Color("hsl(180, 70%, 70%)")
];

class SingleThumbSlider {
  constructor(sliderId) {
    this.slider = document.getElementById(sliderId);
    this.thumb = this.slider.querySelector('.thumb');
    this.tooltip = this.slider.querySelector('.tooltip');
    this.initializeSlider();

    // Listen for changes in the main slider thumbs
    document.addEventListener('thumbsUpdated', () => {
      this.updateThumbColor();
    });
  }

  initializeSlider() {

    this.thumb.addEventListener('mousedown', (event) => {
      event.preventDefault();
      this.onMouseDown(event);

    });

    this.thumb.addEventListener('mouseover', () => {
      const positionInPixels = this.thumb.offsetLeft / this.slider.offsetWidth;
      const tooltipValue = positionInPixels.toFixed(2); // Round to 2 decimal points
      this.tooltip.innerText = tooltipValue;
      this.updateTooltipPosition();
      this.tooltip.style.display = 'block';
    });

    this.thumb.addEventListener('mouseout', () => {
      this.tooltip.style.display = 'none';
    });

    // Mouse move and mouse up listeners
    document.addEventListener('mousemove', (event) => this.onMouseMove(event));
    document.addEventListener('mouseup', () => this.onMouseUp());
  }

  onMouseDown(event) {
    this.isDragging = true;
    this.onMouseMove(event);
  }

  onMouseMove(event) {
    if (!this.isDragging) return;
    const rect = this.slider.getBoundingClientRect();
    const newPosition = (event.clientX - rect.left) / this.slider.offsetWidth;
    var positionInPixels = newPosition * 100;
    positionInPixels = Math.min(Math.max(positionInPixels, 0), 100);
    this.thumb.style.left = `${positionInPixels}%`;
    this.thumb.setAttribute('data-value', `${positionInPixels / 100.0}`);
    this.updateThumbColor();
    updateSingleThumbValue(positionInPixels/100.0);
  }

  onMouseUp() {
    this.isDragging = false;
    this.tooltip.style.display = 'none';
  }

  updateThumbColor() {
    const markers = slider_values;
    const sliderWidth = this.slider.offsetWidth;
    const thumbPosition = this.thumb.offsetLeft / sliderWidth; // Get relative position of the thumb
    let a = 0;
    let b = 1;

    if (markers[0] <= thumbPosition && thumbPosition <= markers[1]) {
      a = 0;
      b = 1;
    } else if (markers[1] <= thumbPosition && thumbPosition <= markers[2]) {
      a = 1;
      b = 2;
    } else if (markers[2] <= thumbPosition && thumbPosition <= markers[3]) {
      a = 2;
      b = 3;
    }

    const t = (thumbPosition - markers[a]) / (markers[b] - markers[a]);

    const colorStart = frameColors[a].clone(); // Clone to avoid modifying the original colors
    const colorEnd = frameColors[b].clone();

    // Interpolate color
    colorStart.lerp(colorEnd, t);

    this.thumb.style.backgroundColor = colorStart.getStyle();
  }

  updateTooltipPosition() {
    const tooltipValue = parseFloat(this.thumb.getAttribute('data-value')).toFixed(2);
    this.tooltip.innerText = tooltipValue;
    this.tooltip.style.left = `${this.thumb.offsetLeft - this.tooltip.offsetWidth / 2 + this.thumb.offsetWidth / 2}px`;
    this.tooltip.style.top = `${this.thumb.offsetTop - this.tooltip.offsetHeight - 10}px`;
  }
}

export default SingleThumbSlider;



