import {gS,
  livecanvas, lctx,
  liveCanvas, lctxs,
  canvas, ctx,
  myH, myW,
  slider_values
} from './main.js';
import { assert } from "hoek";
import { SVGLoader } from "three/examples/jsm/loaders/SVGLoader";

import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass"
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { SobelOperatorShader } from "three/examples/jsm/shaders/SobelOperatorShader";
import { LuminosityShader } from "three/examples/jsm/shaders/LuminosityShader";
import { PostProcessShader } from "./PostProcessShader";
import {drawSpline} from './canvas_utils';
import {imageLoader} from './main.js';

var THREE = require("three");
var OBJExporter = require('three-obj-exporter');

var frameColors = [
  new THREE.Color("hsl(0, 70%, 70%)"),
  new THREE.Color("hsl(60, 70%, 70%)"),
  new THREE.Color("hsl(120, 70%, 70%)"),
  new THREE.Color("hsl(180, 70%, 70%)")
];

// Define the SepiaShader

THREE.SepiaShader = {
  uniforms: {
    "tDiffuse": { value: null },
    "amount": { value: 0.0 },
    "color_new": { value: new THREE.Color(1.0, 0.0, 0.0) }
  },
  vertexShader: [
    "varying vec2 vUv;",
    "void main() {",
    "vUv = uv;",
    "gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);",
    "}"
  ].join("\n"),
  fragmentShader: [
    "uniform float amount;",
    "uniform vec3 color_new;",
    "uniform sampler2D tDiffuse;",
    "varying vec2 vUv;",
    "void main() {",
    "vec4 color = texture2D(tDiffuse, vUv);",
    "if (color.r==0.0 && color.g==0.0 && color.b==0.0)",
    "gl_FragColor = vec4(0.0);",
    "else {",
    "vec3 c = color.rgb;",
    "gl_FragColor = vec4(mix(color.rgb, color_new, 0.75), 1.0);",
    "}",
    "}"
  ].join("\n")
};





export class Voronoi2D {

  // :::::::::: Constructor :::::::::: //
  constructor(canvas_index=0) {

    this.scene = new THREE.Scene();

    this.frustumSize = 1;
    this.aspect = myW/myH;
    this.camera = new THREE.OrthographicCamera(this.frustumSize * this.aspect / -2, this.frustumSize * this.aspect / 2, this.frustumSize / 2, this.frustumSize / -2);
    this.camera.position.z = 10;
    // this.camera.position.y = -.5;
    this.camera.lookAt(this.scene.position);
    this.factor = 1.0;
    this.screenWidth = myW;//window.innerWidth * this.factor;
    this.screenHeight = myH;//window.innerHeight * this.factor;
    this.canvas_index = canvas_index;

    this.renderer = new THREE.WebGLRenderer({preserveDrawingBuffer: true});

    this.renderer.setSize(myW,myH);//this.screenWidth, this.screenHeight);
    this.renderer.setClearColor(0xEEEEEE);

    this.frameIndex = 0.0;
    this.frame_markers = slider_values;
    // this.renderer.setViewport(10,-10,myW,myH);
    // document.body.appendChild(this.renderer.domElement);

    this.canvasVG = document.getElementById("sketch-container");
    this.scaleMultiplier = 1.0;
    //if (canvas_index!=0)  { this.scaleMultiplier/=2.5;this.renderer.setSize(myW*this.scaleMultiplier-50,myH*this.scaleMultiplier);}
    if (canvas_index==1)  { this.canvasVG = document.getElementById("sketch1"); }
    else if (canvas_index==2)  { this.canvasVG = document.getElementById("sketch2");}
    else if (canvas_index==3)  {this.canvasVG = document.getElementById("sketch3");}
    else if (canvas_index==4)  {this.canvasVG = document.getElementById("sketch4");}
    this.canvasVG.appendChild(this.renderer.domElement);

    // :::: Postprocessing :::: //
    this.composer = new EffectComposer(this.renderer);
    const renderPass = new RenderPass(this.scene, this.camera);
    this.composer.addPass(renderPass);

    // Sobel operator

    this.effectSobel = new ShaderPass( PostProcessShader );
    this.effectSobel.uniforms[ 'resolution' ].value.x = myW * window.devicePixelRatio;
    this.effectSobel.uniforms[ 'resolution' ].value.y = myH * window.devicePixelRatio;
    this.composer.addPass( this.effectSobel );

    if (canvas_index==0){
      // Create a custom shader pass using SepiaShader
      this.sepiaShader = new ShaderPass(THREE.SepiaShader);

      // Add the custom shader pass to the composer
      this.composer.addPass(this.sepiaShader);
    }


    // :::: Voronoi elements :::: //
    this.colorHue = 47;
    this.colorPrime = 13;

    var geometry = new THREE.PlaneGeometry(10, 10);
    var material = new THREE.MeshBasicMaterial({ color: this.getRandomHueColor(70) });
    this.plane = new THREE.Mesh(geometry, material);
    this.plane.position.z = 0.2;
    this.scene.add(this.plane);

    this.line_divisions = 6;

    this.coneRadius = 0.6;
    this.coneHeight = 2;
    this.coneSegments = 32;

    this.curveSegments = 16;

    /*VG
    Creating a map below, this.frames,
    key would be the frameNo, or slider Value (ranging between 0-1 for now
    and value would be the points for that frameNo.
    Added 0, and 1 since first and last frame would always be there
     */

    this.frames = new Map();
    this.frames.set(0,[]);
    this.frames.set(1,[]);

    //and a points array for rendering the current frame
    this.points = [];


    this.prismGeometry = {};
    this.createPrismGeometry();

    this.voronoi_lines = [];
    this.isBorder = true;

    // Listen for key press events

    document.addEventListener('keydown', (event) => {
      if (this.canvas_index==1 && (event.key === '1')) {
        this.captureAndSavePNG_SVG();
      }
      if (this.canvas_index==2 && (event.key === '2')) {
        this.captureAndSavePNG_SVG();
      }
      if (this.canvas_index==3 && (event.key === '3')) {
        this.captureAndSavePNG_SVG();
      }
      if (this.canvas_index==4 && (event.key === '4')) {
        this.captureAndSavePNG_SVG();
      }
    });
    this.animate();
  }

  getPoints(){
    return this.frames.get(gS.ctxStyle.level);
  }


  // :::::::::: Rendering :::::::::: //
  render() {
    if(this.isBorder == true) {
      if(this.isSegmentColor == true) {
        this.composer.render();
      } else {
        this.composer.render();
      }
    }
    else {
      this.renderer.render(this.scene, this.camera);
    }
  }

  map_sliderValue(){
        const markers = slider_values;
        const thumb = this.frameIndex;

        var a = 0.0;
        var b = 1.0;

        if (markers[0]<=thumb && thumb<=markers[1])     {a = markers[0]; b = markers[1];}
        else if (markers[1]<=thumb && thumb<=markers[2])     {a = markers[1]; b = markers[2];}
        else if (markers[2]<=thumb && thumb<=markers[3])     {a = markers[2]; b = markers[3];}

        const t = (thumb-a)/(b-a);
        return t;
  }

  lerpColor(){
    const markers = slider_values;
    const thumb = this.frameIndex;

    var a = 0.0, b = 1.0;

    if (markers[0]<=thumb && thumb<=markers[1])     {a = 0; b=1;}//markers[0]; b = markers[1];}
    else if (markers[1]<=thumb && thumb<=markers[2])     {a = 1; b=2;}//{a = markers[1]; b = markers[2];}
    else if (markers[2]<=thumb && thumb<=markers[3])     {a = 2; b=3;}//{a = markers[2]; b = markers[3];}

    const t = (thumb-markers[a])/(markers[b]-markers[a]);

    const lerpColor = new THREE.Color(frameColors[a]);
    lerpColor.lerpHSL(frameColors[b], t);
    this.sepiaShader.uniforms.color_new.value = lerpColor;
  }

  animate() {
    if (this.canvas_index==0) {
      if (this.sepiaShader) {
        this.lerpColor();
      }
    }
    requestAnimationFrame(this.animate.bind(this));
    this.render();
  }

  // Function to capture and save a PNG image
  captureAndSavePNG() {

    // Save the rendered image as a PNG
    const canvas = this.renderer.domElement;
    const dataURL = canvas.toDataURL("image/png");

    const a = document.createElement('a');
    a.href = dataURL;
    a.download = 'render.png';
    a.style.display = 'none';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }

  captureAndSavePNG_SVG() {
    // Save the rendered image as a PNG
    const canvas = this.renderer.domElement;
    const dataURL = canvas.toDataURL("image/png");

    // Convert the data URL to a Blob
    const blob = this.dataURLtoBlob(dataURL);

    // Pass the Blob to the handleFiles_url function
    imageLoader.handleFiles_url(blob,  this.canvas_index);
  }



  dataURLtoBlob(dataURL) {
    const byteString = atob(dataURL.split(',')[1]);
    const mimeString = dataURL.split(',')[0].split(':')[1].split(';')[0];
    const ab = new ArrayBuffer(byteString.length);
    const ia = new Uint8Array(ab);

    for (let i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }

    return new Blob([ab], { type: mimeString });
  }



  renderCurve(curve_id, pts) {

    /* Varda trying to fix points going out
    this.points = [];
    console.log(pts[0]);
    for(var i = 0; i < pts.length; i++){
      var tmp_pt = this.transformCoords(pts[i]);
      this.points.push(new THREE.Vector2(tmp_pt[0],tmp_pt[1]));
    }

     */

    this.points = [];
    for (var i = 0; i < pts.length; i++) {
      var tmp_pt = this.transformCoords(pts[i]);
      if (tmp_pt) {
        this.points.push(new THREE.Vector2(tmp_pt[0], tmp_pt[1]));
      }
    }

    if (this.points.length >= 2) {
      var curve = new THREE.CatmullRomCurve3(this.points.map(pt => new THREE.Vector3(pt.x, pt.y, 0)));
      this.points = curve.getPoints(500);
      this.points = this.points.map(pt => new THREE.Vector2(pt.x, pt.y));

    }

    this.frames.set(Number(gS.ctxStyle.level), this.points);

    if(this.voronoi_lines[curve_id] == null) {
      this.createBezierCurve(curve_id);
      for(var i = 0; i < this.voronoi_lines[curve_id]['cones'].length; i++) {
        this.scene.add(this.voronoi_lines[curve_id]['cones'][i]);
      }
      for(var i = 0; i < this.voronoi_lines[curve_id]['prisms'].length; i++) {
        this.scene.add(this.voronoi_lines[curve_id]['prisms'][i]);
      }
    } else if(this.voronoi_lines[curve_id]['isLineSegment'] == true) {
      this.hideLine(curve_id);
      this.createBezierCurve(curve_id);
      for(var i = 0; i < this.voronoi_lines[curve_id]['cones'].length; i++) {
        this.scene.add(this.voronoi_lines[curve_id]['cones'][i]);
      }
      for(var i = 0; i < this.voronoi_lines[curve_id]['prisms'].length; i++) {
        this.scene.add(this.voronoi_lines[curve_id]['prisms'][i]);
      }
    } else if(this.voronoi_lines[curve_id]['isHidden'] == false) {
      for(var i = 0; i < this.voronoi_lines[curve_id]['cones'].length; i++) {
        this.scene.add(this.voronoi_lines[curve_id]['cones'][i]);
      }
      for(var i = 0; i < this.voronoi_lines[curve_id]['prisms'].length; i++) {
        this.scene.add(this.voronoi_lines[curve_id]['prisms'][i]);
      }
      this.voronoi_lines[curve_id]['isHidden'] = true;
    }

    this.modifyCurve(curve_id);
  }

  renderLine(line_id, point1, point2) {
    point1 = this.transformCoords(point1);
    point2 = this.transformCoords(point2);

    var point_v1 = new THREE.Vector2(point1[0], point1[1]);
    var point_v2 = new THREE.Vector2(point2[0], point2[1]);

    if(this.voronoi_lines[line_id] == null) {
      this.createLine(line_id, point_v1, point_v2);
      this.scene.add(this.voronoi_lines[line_id]['cones'][0]);
      this.scene.add(this.voronoi_lines[line_id]['cones'][1]);
      this.scene.add(this.voronoi_lines[line_id]['prisms'][0]);
    } else if(this.voronoi_lines[line_id]['isLineSegment'] == false) {
      this.hideLine(line_id);
      this.createLine(line_id, point_v1, point_v2);
      this.scene.add(this.voronoi_lines[line_id]['cones'][0]);
      this.scene.add(this.voronoi_lines[line_id]['cones'][1]);
      this.scene.add(this.voronoi_lines[line_id]['prisms'][0]);
    } else if(this.voronoi_lines[line_id]['isHidden'] == true) {
      this.scene.add(this.voronoi_lines[line_id]['cones'][0]);
      this.scene.add(this.voronoi_lines[line_id]['cones'][1]);
      this.scene.add(this.voronoi_lines[line_id]['prisms'][0]);
      this.voronoi_lines[line_id]['isHidden'] = false;
    }
    this.modifyLine(line_id, point_v1, point_v2);
  }

  // :::::::::: Utilities :::::::::: //
  getHSLColor(line_id, S, canvas_index) {

    this.colorHue = (this.colorPrime * line_id) % 360;
    var color1 = new THREE.Color("hsl(" + this.colorHue.toString(10) + ", " + S.toString(10) + "%, 70%)");

    if (canvas_index!=0) {
      if (line_id!= 153) {
          color1 = frameColors[canvas_index-1];
      } else{
          color1 = new THREE.Color("white");
      }
    }

    return color1;
  }

  setNewColor(line_id, canvas_index = 0) {
    this.color = this.getHSLColor(line_id, 70, canvas_index);
  }

  getRandomInt(max) {
    return Math.floor(Math.random() * Math.floor(max));
  }
  getRandomHueColor(S) {
    var color1 = new THREE.Color("hsl(" + this.getRandomInt(361).toString(10) + ", " + S.toString(10) + "%, 70%)");
    return color1;
  }

  getLinePoints(point1, point2, n_divisions) {
    var line = [];
    var t = 0;

    for(var i = 0; i <= n_divisions; i++) {
      var point2D = new THREE.Vector2(0, 0);
      var point2D_1 = new THREE.Vector2(point1.x, point1.y);
      var point2D_2 = new THREE.Vector2(point2.x, point2.y);
      line.push(point2D.addVectors(point2D_1.multiplyScalar(1 - t), point2D_2.multiplyScalar(t)));
      t += 1.0/n_divisions;
    }
    return line;
  }

  createBezierCurve(curve_id) {



    const curve = new THREE.SplineCurve(this.points);
    //drawSpline(lctx,start_v, ctrl_v1, ctrl_v2, end_v);



    //const curve = new THREE.CubicBezierCurve(start_v, ctrl_v1, ctrl_v2, end_v);
    const curve_points = curve.getPoints(this.curveSegments);

    var cone_color = this.color;

    var voronoi_cones = [];
    var voronoi_prisms = [];

    voronoi_cones.push(this.makeSeed(curve_points[0], cone_color));
    for(var i = 1; i < curve_points.length; i++) {

      voronoi_cones.push(this.makeSeed(curve_points[i], cone_color));
      voronoi_prisms.push(this.makePrism(curve_points[i - 1], curve_points[i], cone_color));
    }
    this.voronoi_lines[curve_id] = {
      'id': curve_id,
      'color': cone_color,
      'cones': voronoi_cones,
      'prisms': voronoi_prisms,
      'isHidden': false,
      'isLineSegment': false
    };
  }

  createLine(line_id, point_v1, point_v2) {
    var cone_color = this.color;
    var voronoi_cones = [];
    voronoi_cones.push(this.makeSeed(point_v1, cone_color));
    voronoi_cones.push(this.makeSeed(point_v2, cone_color));

    var voronoi_prisms = [this.makePrism(point_v1, point_v2, cone_color)];
    this.voronoi_lines[line_id] = {
      'id': line_id,
      'color': cone_color,
      'cones': voronoi_cones,
      'prisms': voronoi_prisms,
      'isHidden': false,
      'isLineSegment': true
    };
  }

  makePrism(point_v1, point_v2, prism_color) {
    var material = new THREE.MeshBasicMaterial({
      color: prism_color,
      polygonOffset: true,
      polygonOffsetFactor: 1, // positive value pushes polygon further away
      polygonOffsetUnits: 1
    });

    var prism = new THREE.Mesh(this.prismGeometry, material);
    prism.position.set(point_v1.x, point_v1.y, 0.0);
    prism.scale.z = point_v1.distanceTo(point_v2);
    prism.rotateX(Math.PI / 2);

    return prism;
  }

  makeSeed(point, cone_color) {
    var geometry = new THREE.ConeGeometry(this.coneRadius, this.coneHeight, this.coneSegments);
    var material = new THREE.MeshBasicMaterial({
      color: cone_color,
      polygonOffset: true,
      polygonOffsetFactor: 1, // positive value pushes polygon further away
      polygonOffsetUnits: 1
    });
    var cone = new THREE.Mesh(geometry, material);

    cone.position.x = point.x;
    cone.position.y = point.y;
    cone.position.z = this.coneHeight / 2;
    cone.rotation.x = Math.PI/2;

    return cone;
  }

  modifyLine(line_id, point_v1, point_v2) {
    assert(this.voronoi_lines[line_id] != null, "Cannot find the line.");

    this.voronoi_lines[line_id]['cones'][0].position.set(point_v1.x, point_v1.y, this.coneHeight / 2);
    this.voronoi_lines[line_id]['cones'][1].position.set(point_v2.x, point_v2.y, this.coneHeight / 2);

    var negative_point_v2 = new THREE.Vector2(point_v2.x, point_v2.y);
    negative_point_v2.negate();
    var vec1_2 = new THREE.Vector2();
    vec1_2.addVectors(point_v1, negative_point_v2);
    this.voronoi_lines[line_id]['prisms'][0].rotation.y = Math.atan(vec1_2.y / vec1_2.x) + Math.PI / 2;
    if(vec1_2.x >= 0) {
      this.voronoi_lines[line_id]['prisms'][0].rotation.y += Math.PI;
    }
    this.voronoi_lines[line_id]['prisms'][0].scale.z = point_v1.distanceTo(point_v2);
    this.voronoi_lines[line_id]['prisms'][0].position.set(point_v1.x, point_v1.y, 0);
  }

  modifyCurve(curve_id) {
    assert(this.voronoi_lines[curve_id] != null, "Cannot find the line.");


    const curve = new THREE.SplineCurve(this.points);
    //const curve = new THREE.CubicBezierCurve(start_v, ctrl_v1, ctrl_v2, end_v);

    const curve_points = curve.getPoints(this.curveSegments);

    this.voronoi_lines[curve_id]['cones'][0].position.set(curve_points[0].x, curve_points[0].y, this.coneHeight / 2);

    for(var i = 1; i < curve_points.length; i++) {
      this.voronoi_lines[curve_id]['cones'][i].position.set(curve_points[i].x, curve_points[i].y, this.coneHeight / 2);

      var negative_point_v2 = new THREE.Vector2(curve_points[i].x, curve_points[i].y);
      negative_point_v2.negate();
      var vec1_2 = new THREE.Vector2();
      vec1_2.addVectors(curve_points[i - 1], negative_point_v2);
      this.voronoi_lines[curve_id]['prisms'][i - 1].rotation.y = Math.atan(vec1_2.y / vec1_2.x) + Math.PI / 2;
      if(vec1_2.x >= 0) {
        this.voronoi_lines[curve_id]['prisms'][i - 1].rotation.y += Math.PI;
      }
      this.voronoi_lines[curve_id]['prisms'][i - 1].scale.z = curve_points[i - 1].distanceTo(curve_points[i]);
      this.voronoi_lines[curve_id]['prisms'][i - 1].position.set(curve_points[i - 1].x, curve_points[i - 1].y, 0);
    }
  }

  hideLine(line_id) {
    if(this.voronoi_lines[line_id] != null && this.voronoi_lines[line_id]['isHidden'] == false) {
      for(var i = 0; i < this.voronoi_lines[line_id]['cones'].length; i++) {
        this.scene.remove(this.voronoi_lines[line_id]['cones'][i]);
      }
      for(var i = 0; i < this.voronoi_lines[line_id]['cones'].length; i++) {
        this.scene.remove(this.voronoi_lines[line_id]['prisms'][i]);
      }
      this.voronoi_lines[line_id]['isHidden'] = true;
    }
  }


  //PROBABLY THIS IS THE CULPRIT _ VARDA
  transformCoords(point) {

    let rect = liveCanvas[0].getBoundingClientRect();

    // point[0] += rect.left;
    // point[1] += rect.top;

    // var x = -1 * (this.screenWidth/2 - point[0]) * this.frustumSize * this.aspect / this.screenWidth;
    // var y = (this.screenHeight/2 - point[1]) * this.frustumSize / this.screenHeight;
    var tmpW = myW, tmpH = myH;

    var x = -1 * (tmpW/2 - point[0]) * this.frustumSize * this.aspect / tmpW;
    var y = (tmpH/2 - point[1]) * this.frustumSize / tmpH;

    return [x,y];
    // if (this.canvas_index==0)     return [x, y];
    // return [x*1.75-0.2, y*1.75-0.1];

  }

  removeAllObjects() {
    while(this.voronoi_lines.length) {
      // console.log(this.voronoi_lines[this.voronoi_lines.length - 1]);
      this.voronoi_lines.pop();
    }
  }



  resetColor() {
    this.colorHue = 47;
  }

  createPrismGeometry() {
    var shape = new THREE.Shape();
    shape.moveTo(-1 * this.coneRadius, 0);
    shape.lineTo(0, this.coneHeight);
    shape.lineTo(this.coneRadius, 0);
    shape.lineTo(-1 * this.coneRadius, 0);

    this.prismGeometry = new THREE.ExtrudeGeometry(shape, {amount: 1, bevelEnabled: false});
  }
}



